import styled from 'styled-components';
import texture from  './../../images/img.jpg';

export const Container = styled.section`
    min-height: 100vh;
    padding: 10px;
    color:var(--textPrimaryColor); 
    
    position: relative;   

    .titleProd{
        display:flex;
        width:100vw;
        justify-content:center;
        align-items:center;
        text-transform:uppercase;
        padding:15px;
        font-size:2em;
        font-weight:600;
        text-align:center;
    }
    

    .grid{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        align-items:initial;
        grid-gap:10px;
        *{
            -webkit-touch-callout: none;
            -webkit-user-select: none;
            -khtml-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
        }
    }
    :after {
        content: "";
        background: url(${texture});
        background-position:center;
        background-size:120px;
        opacity: 0.25;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        position: absolute;
        z-index: -1;   
    }
    .btBack{
        background-color:white;
        text-decoration:none;
        width:40px;
        height:40px;
        overflow:hidden;
        font-size:30px;
        display:flex;
        justify-content:center;
        align-items:center;
        border-radius:100%;
        text-align:center;
        color:#272727;
        font-weight:bolder;
        transform:rotateY(180deg);
        cursor: pointer;
    }

    .tool{
        overflow:hidden;
       
        
    }
    

    @media screen and (max-width: 1024px) {
        /*estilos*/
        .grid{
            grid-template-columns: repeat(2,1fr);
        }
    }
    @media screen and (max-width: 800px) {
        /*estilos*/
        .grid{
            grid-template-columns: repeat(1,1fr);
            width:80vw;
            margin:0px auto;
            align-items:initial;
        }
        a.btBack{
            position:static;
        }
    }
`;

export const Item = styled.div`
    
    transition: 0.1s ease-in;
    cursor: pointer;
    *{
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
    display:flex;
    flex-direction:column;
    .add{
        position:absolute;
        z-index:2;
        height:50px;
        top:10px;
        right:10px;
        transition:.3s ease;
        border-radius:100%;
        :hover{
            box-shadow:0px 0px 10px white;
            transform:scale(.9);
        }
    }

    .desc{
        display:flex;
        flex-direction:column;
        justify-content: center;
        text-align:center;
        background-color:whitesmoke;
        color:black;
        font-weight:bold;
        font-size:1.5em;
        
        .pack{
            min-height:min-content;
            padding:15px;
            box-shadow: inset 0px -5px rgba(0,0,0,.5);
            overflow:hidden;
            
        }
    }
    .disable{
        display:none
    }

    
`;

export const Prod = styled.div`
    
    font-weight:600;
    text-align:center;
    min-height:calc(1vh + 100px);
    width:100%;

    position: relative;   

    background: url(${props => props.image ? props.image : "" });
    background-position:center;
    background-size:cover;
    background-color:blueviolet;
    
            
    

    :hover{
        box-shadow: inset 0px -5px rgba(27,27,27,.5);
        :after{
            background-color:rgba(27,27,27,.5);
        }
    }
    :after {
        content: " ${props => props.text ? props.text : "" }";
        background-color:rgba(27,27,27,.3);
        display:flex;

        padding:15px;
        justify-content:center;
        align-items:center;
        text-transform:uppercase;
        color:white;
        font-weight:bolder;
        font-size:2em;
        text-align:center;

        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        position: absolute;
        z-index: 1;   
        opacity:1;

        transition:.3s ease;

        
    }

    .processo{
        position: absolute;
        display:flex;
        flex-direction:column;
        justify-content:center;
        align-items:center;
        z-index:2;
        left:0;
        right:0;
        bottom:0;
        font-size:20px;
        padding: 5px;
        background-color:var(--primaryColor);
        color:var(--backColor);
        img{
            width:30px;
        }
    }

`;
