import React,{Component} from 'react';
import {Container,Item,Prod} from './styled';
import $ from 'jquery';
import ReactTooltip from 'react-tooltip'


import prod from '../../produtos';
import add from  '../../images/add.svg';
import arrow from './../../images/arrowBlack.svg';


class Product extends Component {
    
    constructor(props){
        super(props);
        this.state={
            id: decodeURI(props.match.params.id),
        };
        this.prod = prod;
        this.products=this.prod[this.state.id];
        this.mostrar= this.mostrar.bind(this);
        
    }


    render(){

        return (
            <Container className="product">
                <button  onClick={()=>window.history.back()} className="btBack"> ➜ </button>
                <div className="titleProd">{this.products[0]}</div>
                <div className="grid">
                {this.products.map((item,pos)=>{
                    var desc=[];
                    for( var i in item.pack){
                        desc.push(<div className="pack" key={item.key+""+i}>{item.pack[i]}</div>);
                    }

                    if(item.title)
                        return <Item className="item" key={item.key}>
                                    
                                    <Prod onClick={()=>{this.mostrar(pos-1)}} className="title" image={item.image} text={item.title} >
                                        <img className="add" src={add} alt="" data-tip="Adicionar ao carrinho" >
                                        </img>
                                        
                                        <span className="processo">PROCEDIMENTOS <img src={arrow} alt=""/></span>
                                    </Prod>
                                    <div className="desc">{desc}</div>
                                </Item>
                })}
                </div>

                <ReactTooltip 
                    className="tool" 
                    place="bottom" 
                    type="light" 
                    effect="solid"
                    
                    // getContent={(dataTip) => 
                    // <button onClick={}>{dataTip}</button> }
                />
            </Container>
        );
    }




    componentDidUpdate(){
        window.location.reload();
    }

    componentDidMount(){
        $('html, body').animate({
            scrollTop:0
        }, 1000);
        $(".desc").hide();
        
        $(".item .title").each((index)=>{
            $(".item .title").eq(index).height($(".item .title").eq(index).width());
        });

        $(window).resize(function () { 
            $(".item .title").each((index)=>{
                $(".item .title").eq(index).height($(".item .title").eq(index).width());
            });
        });


        $(".add").click((e)=>{        

            let prod={
                id:[this.state.id],
                key:[$(e.target).parent().parent().index()+1]
            };


            if(localStorage.getItem("prod")){
                
                let lastProd={
                    id: [...JSON.parse(localStorage.getItem("prod")).id],
                    key: [...JSON.parse(localStorage.getItem("prod")).key],
                };
                let pode = true;
                console.log(prod);
                for( var i in lastProd.id){
                    if(lastProd.key[i] == prod.key && lastProd.id[i] == prod.id){
                        pode = false;
                    }
                        
                }


                if(pode){
                    lastProd.id.push(...prod.id);
                    lastProd.key.push(...prod.key);
                }

                localStorage.setItem("prod",JSON.stringify(lastProd));
                console.log(lastProd);
            }
            else{
                localStorage.setItem("prod",JSON.stringify(prod));
            }
        });
    }
    


    mostrar(pos){
        if( $(".desc").eq(pos).is(':visible')){
            $(".desc").eq(pos).slideUp( "slow");
            $(".processo img").eq(pos).css({
                transform:"rotate("+0+"deg)"
            });
            console.log($(".processo img"));
        }
        else if( $(".desc").eq(pos).is(':hidden')){
            $(".desc").eq(pos).slideDown( "slow");
            $(".processo img").eq(pos).css({
                transform:"rotateZ("+180+"deg)"
            });
        }
    }

}

export default Product;
